import { DeviceFinderInterface, DeviceSearchCriteria, DeviceDescriptor } from '@transport/interfaces/endpoint.interface'

/**
 * WebUSB device finder implementation
 */
export class WebUSBDeviceFinder implements DeviceFinderInterface {
    /**
     * Find USB devices matching criteria
     */
    async findDevices(criteria: DeviceSearchCriteria): Promise<DeviceDescriptor[]> {
        const devices = await navigator.usb.getDevices()
        console.log('[WebUSBDeviceFinder] Got devices from getDevices():', devices)
        console.log('[WebUSBDeviceFinder] Search criteria:', criteria)

        return devices
            .filter(device => {
                // Handle vendorId = 0 as "any vendor"
                if (criteria.vendorId !== undefined && criteria.vendorId !== 0 && device.vendorId !== criteria.vendorId) {
                    return false
                }
                // Handle productId = 0 as "any product"
                if (criteria.productId !== undefined && criteria.productId !== 0 && device.productId !== criteria.productId) {
                    return false
                }
                // WebUSB doesn't easily expose interface class info without opening device
                return true
            })
            .map(device => ({
                device,
                vendorId: device.vendorId,
                productId: device.productId,
                manufacturer: device.manufacturerName || undefined,
                product: device.productName || undefined,
                serialNumber: device.serialNumber || undefined,
            }))
    }

    /**
     * Request device access (WebUSB only)
     */
    async requestDevice(criteria: DeviceSearchCriteria): Promise<DeviceDescriptor> {
        const filters: USBDeviceFilter[] = []
        
        // If looking for PTP devices (cameras)
        if (criteria.class === 6) {
            // Request any device with PTP class
            filters.push({ classCode: 6 })
        } else if (criteria.vendorId !== undefined && criteria.vendorId !== 0) {
            filters.push({ vendorId: criteria.vendorId })
            if (criteria.productId !== undefined && criteria.productId !== 0) {
                filters[0] = { ...filters[0], productId: criteria.productId }
            }
        } else {
            // If no specific criteria, request PTP devices by default
            filters.push({ classCode: 6 })
        }

        console.log('[WebUSBDeviceFinder] Requesting device with filters:', filters)
        const device = await navigator.usb.requestDevice({ filters })

        return {
            device,
            vendorId: device.vendorId,
            productId: device.productId,
            manufacturer: device.manufacturerName || undefined,
            product: device.productName || undefined,
            serialNumber: device.serialNumber || undefined,
        }
    }

    /**
     * Get all available USB devices
     */
    async getAllDevices(): Promise<DeviceDescriptor[]> {
        const devices = await navigator.usb.getDevices()
        return devices.map(device => ({
            device,
            vendorId: device.vendorId,
            productId: device.productId,
            manufacturer: device.manufacturerName || undefined,
            product: device.productName || undefined,
            serialNumber: device.serialNumber || undefined,
        }))
    }
}