'use client'

import { Camera, listCameras, requestCameraAccess, CameraDescriptor } from '../../../dist/web'
import { useEffect, useState } from "react";

export default function Home() {
  const [cameras, setCameras] = useState<CameraDescriptor[]>([])
  const [error, setError] = useState<string>('')
  const [connected, setConnected] = useState(false)
  const [hasWebUSB, setHasWebUSB] = useState(false)

  // Check WebUSB support after mounting (to avoid hydration issues)
  useEffect(() => {
    setHasWebUSB(!!navigator.usb)
  }, [])

  // Check for already paired devices
  const checkCameras = async () => {
    try {
      // First check what WebUSB sees directly
      if (navigator.usb) {
        const usbDevices = await navigator.usb.getDevices()
        console.log('Raw USB devices from navigator.usb.getDevices():', usbDevices)
      }
      
      const cams = await listCameras()
      console.log('Found cameras from listCameras():', cams)
      setCameras(cams)
    } catch (err) {
      console.error('Error listing cameras:', err)
      setError(err?.toString() || 'Failed to list cameras')
    }
  }

  useEffect(() => {
    checkCameras()
  }, [])

  // Request permission for a new USB device
  const requestCameraPermission = async () => {
    try {
      // Use the library's requestCameraAccess function
      const camera = await requestCameraAccess()
      
      if (camera) {
        console.log('Camera access granted:', camera)
        // Add the newly granted camera to the list immediately
        setCameras(prev => [...prev, camera])
        // Also refresh the full list
        setTimeout(checkCameras, 100)
      } else {
        setError('No camera selected')
      }
    } catch (err: any) {
      console.error('Error requesting device:', err)
      setError(err?.toString() || 'Failed to request device')
    }
  }

  // Connect to a camera
  const connectCamera = async () => {
    try {
      const camera = new Camera()
      await camera.connect()
      setConnected(true)
      console.log('Connected to camera!')
    } catch (err) {
      console.error('Error connecting:', err)
      setError(err?.toString() || 'Failed to connect')
    }
  }

  return (
    <div className="font-sans grid grid-rows-[auto_1fr_auto] items-center justify-items-center min-h-screen p-8 gap-8">
      <div className="text-center">
        <h1 className="text-2xl font-bold mb-4">WebUSB Camera Control</h1>
        
        {/* Show WebUSB support status */}
        <div className="mb-4">
          WebUSB Support: {hasWebUSB ? ' Available' : ' Not Available'}
        </div>

        {/* Permission request button */}
        <button 
          onClick={requestCameraPermission}
          className="bg-blue-500 text-white px-4 py-2 rounded mr-2 hover:bg-blue-600"
        >
          Request Camera Access
        </button>

        {/* Refresh button */}
        <button 
          onClick={checkCameras}
          className="bg-gray-500 text-white px-4 py-2 rounded mr-2 hover:bg-gray-600"
        >
          Refresh Camera List
        </button>

        {/* Connect button */}
        {cameras.length > 0 && !connected && (
          <button 
            onClick={connectCamera}
            className="bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600"
          >
            Connect to First Camera
          </button>
        )}
      </div>

      {/* Camera list */}
      <div className="text-center">
        <h2 className="text-xl mb-2">Available Cameras: {cameras.length}</h2>
        {cameras.length === 0 ? (
          <p className="text-gray-500">
            No cameras found. Click "Request Camera Access" to grant permission.
          </p>
        ) : (
          <div className="space-y-2">
            {cameras.map((camera, idx) => (
              <div key={camera.serialNumber || idx} className="border p-2 rounded">
                <div>Vendor: {camera.vendor}</div>
                <div>Model: {camera.model}</div>
                <div>Serial: {camera.serialNumber || 'N/A'}</div>
                {camera.usb && (
                  <div className="text-sm text-gray-600">
                    USB: {camera.usb.vendorId.toString(16)}:{camera.usb.productId.toString(16)}
                  </div>
                )}
              </div>
            ))}
          </div>
        )}

        {connected && (
          <div className="mt-4 text-green-600 font-bold">
             Camera Connected!
          </div>
        )}
      </div>

      {/* Error display */}
      {error && (
        <div className="text-red-500 text-center">
          Error: {error}
        </div>
      )}
    </div>
  );
}
