import { Photo as PhotoType } from './types'

export class Photo implements PhotoType {
    public data: ArrayBuffer | Uint8Array | Buffer
    public filename: string
    public size: number
    public capturedAt: Date

    constructor(data: ArrayBuffer | Uint8Array | Buffer, filename: string, capturedAt?: Date) {
        this.data = data
        this.filename = filename
        this.size = data instanceof ArrayBuffer ? data.byteLength : data.length
        this.capturedAt = capturedAt || new Date()
    }

    async save(filename?: string): Promise<void> {
        const blob = this.toBlob()
        const url = URL.createObjectURL(blob)
        const a = document.createElement('a')
        a.href = url
        a.download = filename || this.filename
        a.click()
        URL.revokeObjectURL(url)
    }

    toBlob(): Blob {
        // Ensure data is in the correct format for Blob constructor
        let blobData: ArrayBuffer
        if (this.data instanceof ArrayBuffer) {
            blobData = this.data
        } else {
            // Convert typed array to ArrayBuffer
            blobData = this.data.buffer.slice(
                this.data.byteOffset, 
                this.data.byteOffset + this.data.byteLength
            ) as ArrayBuffer
        }
        return new Blob([blobData], { type: 'image/jpeg' })
    }

    async toFile(): Promise<File> {
        // Ensure data is in the correct format for File constructor
        let fileData: ArrayBuffer
        if (this.data instanceof ArrayBuffer) {
            fileData = this.data
        } else {
            // Convert typed array to ArrayBuffer
            fileData = this.data.buffer.slice(
                this.data.byteOffset,
                this.data.byteOffset + this.data.byteLength
            ) as ArrayBuffer
        }
        return new File([fileData], this.filename, { 
            type: 'image/jpeg',
            lastModified: this.capturedAt.getTime()
        })
    }

    toJSON() {
        return {
            filename: this.filename,
            size: this.size,
            capturedAt: this.capturedAt,
            dataSize: this.size,
        }
    }
}